import React from 'react';
import { useTranslation, Trans } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import axios from 'axios';

// Form
import { useStateMachine } from 'little-state-machine';

// Hooks
import { useModal } from 'hooks/useModals';

// Modals
import ConfirmationModal from 'modals/ConfirmationModal';

// Components
import WizardButtons from 'components/WizardButtons';

// Update Action
import { resetStore } from 'utils/wizard';

// Header Control
import useHeaderContext from 'hooks/useHeaderContext';

// Utils
import { scrollToTop } from 'helper/scrollHelper';

// Styles
import { colors } from '../../../../theme';
import {
  Title,
  ImageProcessing,
  ProcessingContainer,
  PredictionResultContainer,
  TitleResult,
  ImagePredictionResult,
  VLogo,
  SubmitError,
  IntroText,
  SubmissionIdBox,
  SubmissionId,
  ButtonContainer,
} from './style';

const predictionEndpointUrl = process.env.REACT_APP_PREDICTION_ENDPOINT || '';

const PredictionResult = () => {
  // Hooks
  const {
    setDoGoBack, setTitle, setSubtitle, setType,
  } = useHeaderContext();
  const { t } = useTranslation();
  const { state, actions } = useStateMachine({ resetStore: resetStore() });
  const history = useHistory();

  // States
  const [errorCode, setErrorCode] = React.useState<string | null>(null);
  const [processing, setProcessing] = React.useState<boolean>(true);
  const [prediction, setPrediction] = React.useState<string>('unknown');
  const [submissionId, setSubmissionId] = React.useState<string>('');
  const [submitError, setSubmitError] = React.useState<string | null>(null);

  const { isOpen, openModal, closeModal } = useModal();

  React.useEffect(() => {
    // Hide the Footer Report Problems while processing
    const target = document && document.getElementById('footer-report-problems');
    if (target) {
      if (processing) {
        target.style.display = 'none';
      } else {
        target.style.display = 'flex';
      }
    }
  }, [processing]);

  // Handlers
  const handleSubmit = async () => {
    try {
      setSubmitError(null);
      if (state && state.welcome && state['submit-steps']) {
        const {
          recordYourCough,
        } = state['submit-steps'];

        const body = new FormData();

        // Records
        const coughFile = recordYourCough?.recordingFile || recordYourCough?.uploadedFile;
        if (coughFile) {
          body.append('cough', coughFile, coughFile.name || 'filename.wav');
        }

        body.append('passcode', state.welcome?.passcode ?? '');

        body.append('patientId', 'predict-app');

        const predictionResult = await axios.post(predictionEndpointUrl, body, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
        if (predictionResult.data) {
          setProcessing(false);
          setPrediction(predictionResult.data.prediction);
          setSubmissionId(predictionResult.data.submissionId);
          setErrorCode(predictionResult.data.errorCode);
          actions.resetStore({});
        }
      } else {
        actions.resetStore({});
      }
    } catch (err) {
      console.log('Error', err);
      setSubmitError(t('predictionResult:submitError'));
    }
  };

  const handleReturnMain = React.useCallback(() => {
    history.replace('');
  }, [history]);

  // Effects
  React.useEffect(() => {
    scrollToTop();
    setTitle('');
    setDoGoBack(() => {});
    handleSubmit();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (processing) {
      setSubtitle('');
      setType('noShape');
    } else {
      setSubtitle(`${t('predictionResult:result')}`);
      setType('shapeUp');
      openModal();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [processing]);

  const renderResult = React.useMemo(() => {
    if (prediction === 'positive') {
      return (
        <>
          <TitleResult color={colors.red}>
            {t('predictionResult:resultDetected')}
          </TitleResult>
          <IntroText>
            <Trans i18nKey="predictionResult:resultDetectedTextt">
              Your voice has indicators of COVID-19.
              <strong>Please contact your healthcare professional</strong> and take additional precautions.
            </Trans>
          </IntroText>
          <ImagePredictionResult />
        </>
      );
    }
    if (prediction === 'negative') {
      return (
        <>
          <TitleResult color={colors.green}>
            {t('predictionResult:resultNotDetected')}
          </TitleResult>
          <IntroText>
            <Trans i18nKey="predictionResult:resultNotDetectedText">
              Your voice does not seem to have indicators of COVID-19. Please
              <strong>continue to take appropriate measures</strong>
              based on the advice of your healthcare professional or applicable
              regulatory body and reassess yourself in our app daily.
            </Trans>
          </IntroText>
          <VLogo />
        </>
      );
    }
    return (
      <>
        <TitleResult color={colors.black}>
          {t('predictionResult:resultNotAnalyze')}
        </TitleResult>
        <IntroText>
          <Trans i18nKey="predictionResult:resultNotAnalyzeText">
            Our algorithm is not able to determine your COVID-19 status. <strong>Please submit another cough.</strong>
          </Trans>
        </IntroText>
        <ButtonContainer>
          <WizardButtons
            invert
            leftLabel={t('predictionResult:returnMain')}
            leftHandler={handleReturnMain}
          />
        </ButtonContainer>
        <ImagePredictionResult />
      </>
    );
  }, [handleReturnMain, prediction, t]);

  return (
    <>
      {(errorCode || submitError) && (
      <SubmitError>
        {errorCode ? `${t('predictionResult:error')} ${errorCode}` : submitError}
      </SubmitError>
      )}
      {
        processing ? (
          <ProcessingContainer>
            {/* Title */}
            <Title>
              {t('predictionResult:processingTitle')}
            </Title>

            {/* Image */}
            <ImageProcessing />
          </ProcessingContainer>
        ) : (
          <>
            <ConfirmationModal
              isOpen={isOpen}
              modalTitle={t('predictionResult:note')}
              toggle={closeModal}
              onConfirm={closeModal}
            >
              <Trans i18nKey="predictionResult:resultModal">
                This app will not predict your COVID-19 status or diagnose any disease, disorder,
                or other health condition. Virufy is conducting research and will use the information
                you provide for that research only. Virufy will not take place of a doctor and would like to
                remind you it is your responsiblity to seek medical advice from your doctor.
              </Trans>
            </ConfirmationModal>
            {submissionId && (
              <SubmissionIdBox>
                <SubmissionId>
                  <Trans i18nKey="predictionResult:result_submission">
                    Your unique submission ID:
                    <br />
                    <strong>{{ submissionId }}</strong>
                  </Trans>
                </SubmissionId>
              </SubmissionIdBox>
            )}
            <PredictionResultContainer>
              {renderResult}
            </PredictionResultContainer>
          </>
        )
      }
    </>
  );
};

export default React.memo(PredictionResult);
